/*GENERAL*/

html {font-size: 62.5% ; font-family: 'Krub', sans-serif; background-image: url(../imagenes/repetido.png)}

body {font-size: 1.5rem ; border-left: 0 ; color: blanchedalmond;
}
main {background-color: rgba(70, 60, 80, 0.418)}

header {background-color: rgb(0, 0, 0) ;position: fixed; top: 0 ; width: 100%}

h3 {color:coral}
h2 {background-color:coral; color: rgb(0, 0, 0);
text-align: left;
padding: 10px} 

h3 {color:coral}

.universe-main { /*PARA CONTROLAR CADA SECCION*/
    padding: 0;
    text-align: center
    }

    .wrapper {
        margin-top:0;
        padding-top: 60px;
        padding-bottom: 60px;
        width: 100% ;
        

    }

    * {
        margin: 0;
        box-sizing: border-box;
    }

/*MENU NAVEGADOR*/
nav {
        display: grid;
        grid-template-columns: 1fr 0.8fr 1fr 1fr 1.2fr 1fr;
        grid-auto-rows: auto;
        grid-gap: 1em;
        align-items: center;
        text-align: center;
      
      }
      
      .logo {
        grid-column: 1;
        background-image: url(https://upload.wikimedia.org/wikipedia/commons/thumb/d/d0/Steven_Universe_logo.svg/1200px-Steven_Universe_logo.svg.png);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        height: 10vh;
      }
      
      .navLinks {
        font-size: 1.2em;
        text-transform: uppercase;
        text-decoration: none;
        color: blanchedalmond
      }
      
      .navLinks:hover {
        color: chocolate;
        
        padding: 1em 1em;
      }

