/*PERSONAJES*/

/*PERSONAJES*/

img {width: 20rem}

    .personajes-flex {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        ;
       
        justify-content: center;
    
        
    }

  
.foto-circular-personaje {
    width: 18rem;
    height: 18rem;
    border-radius: 50%;
    overflow: hidden;
    border-color:  coral;
    border-style: solid;
    border-width: 0.4rem;
    margin: auto;
    transition-duration: 0.5s ;
    transition-timing-function: linear;
    transition-delay:  0s;
}

.foto-circular-personaje:hover{transform:scale(1.03);border-width:0.7rem; border-color: yellow

}

div {
    display: block;
}

.column-personajes .linea-negra {
    border-style: solid;
    border-bottom: black;
    border-width: 0.1rem;
    
    margin: 1rem 0 1rem 0}


.column-personajes{
        width: 100%;
        margin-top: 5rem ;
        
        padding: 5rem 0 0 0; }
        

  /*FOOTER*/      

  footer {background-color: rgb(0, 0, 0) ; font-size:5rem ; text-align: center ; color: black }
  



 /*PARA ESCRITORIO*/       
@media screen and (max-width: 900px) {
    nav {
      grid-template-columns: 100%;
      grid-template-rows: auto;
      grid-gap: 1em;
      border-left:none ;
    border-right : none 
   }
  
}
 @media screen and (min-width:1200px){
.column-personajes .linea-negra {
    border-style: solid;
    border-bottom: black;
    border-width: 0.1rem;
    margin: 1rem 0 1rem 0;
}

.column-personajes {
    width: 300px;
    padding: 3rem 0 2rem 0;
}

}


